<template lang="pug">
.page-container
    .page-title 個案列表
    el-table.list-container(:data="cases" style="width: 100%;")
        el-table-column(prop="name" label="姓名")
        el-table-column(prop="caseId" label="學號")
        //- el-table-column(prop="available_time" label="可諮商時段")
        //-     template(#header='')
        //-         el-popover(placement="right" title="" :width="200" trigger="hover" popper-class="weektimePop")
        //-             div(v-html="getHtmlContent()")
        //-             template(#reference)
        //-                 span 可諮商時段
        //-                     QuestionFilled(style="width: 1em; height: 1em; padding: 0 0 0 3px")
        //-     template(#default='scope')
        //-         div {{scope.row.available_time}}
        //- el-table-column(prop="crisis_level" label="危機程度" sortable :sort-method="levelRule")
        el-table-column(prop="init_counselor_name" label="初談老師")
        el-table-column(prop="discription" label="狀態說明")
        el-table-column(prop="created_date" label="初談時間" sortable)
            template(#default='scope')
                div {{scope.row.created_date.split('T')[0]}}
        el-table-column(label="是否派案")
            template(#default='scope')
                el-tag(v-if="scope.row.status=='closed'" type="info" size="small") 已結案
                el-tag(v-else-if="scope.row.status=='assign'" size="small") 諮商中
                el-tag(v-else type="danger" size="small") 未派案
        el-table-column(prop="counselor_name" label="派案老師")
            template(#default='scope')
                div(v-if="scope.row.status!='unassign'") {{scope.row.counselor_name}}
                .empty-cell(v-else) 無資料
        el-table-column(label="諮商時段")
            template(#default='scope')
                div(v-if="scope.row.status!='unassign'") {{scope.row.consultation_day + scope.row.consultation_time}}
                .empty-cell(v-else) 無資料
        el-table-column(prop="first_consultation_date" label="諮商開始時間" width="180px")
            template(#default='scope')
                el-date-picker(v-if="scope.row.status!='unassign'" v-model="form[scope.row.id]" type="date" size="mini" style="width: 80%;")
                .empty-cell(v-else) 無資料
        el-table-column(label="諮商次數" width="80px")
            template(#default='scope')
                div(v-if="scope.row.status!='unassign'") {{countConsultationNumber(getDateStr(0), form[scope.row.id])}}
                .empty-cell(v-else) 無資料
        el-table-column(label="功能按鈕" align="center" width="150px")
            template(v-slot:default="slotProps") 
                el-button(type="success" size="mini"  @click="modifyCase(slotProps.row.id)") 修改
                el-button(type="danger" size="mini" @click="clickDelete(slotProps.row)") 刪除
</template>

<script>
import {weektimeMixin} from "@/components/weektimeMixin"
export default {
    mixins: [weektimeMixin],
    data () {
        return {
            form: {
            }
        }
    },
    async created() {
        await this.$store.dispatch('fetchCases')
        this.cases.forEach(element => {
            var today = new Date(element.first_consultation_date)
            // today.setDate(today.getDate() + 1)
            this.form[element.id] = today
        });
    },
    computed: {
        cases() {
            return this.$store.state.cases
        }
    },
    methods: {
        getDateStr(date){
            var today
            if(date!=0)
                today = new Date(date)
            else
                today = new Date()
            // today.setDate(today.getDate() + offset)
            return today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate()
        },
        deleteCase(id) {
            this.$store.commit('SET_STATUS', 'pending')
            this.$http.delete("/api/cases/"+id)
            .then(response => {
                console.log(response.data.message)
                this.$message({
                    type: 'success',
                    message: '刪除成功!'
                });
                this.$store.dispatch('fetchCases')
                this.$store.commit('SET_STATUS', 'end')
            })
            .catch((error) => {
                //發送請求失敗時
                console.log(error)
            });
        },
        clickDelete(caseData) {
            this.$confirm('將永久刪除學號為 '+caseData.caseId+' 的資料, 是否繼續?', '提示', {
                confirmButtonText: '確定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                    this.deleteCase(caseData.id)
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消刪除'
                })         
            })
        },
        modifyCase(id) {
            this.$store.commit('SET_STATUS', 'pending')
            this.$http.put("/api/cases/"+id,{
                first_consultation_date: this.getDateStr(this.form[id])
            })
            .then(response => {
                console.log(response.data.message)
                this.$store.dispatch('fetchCases')
                this.$store.commit('SET_STATUS', 'end')
            })
            .catch((error) => {
                //發送請求失敗時
                console.log(error)
            });
        },
        levelRule(aObj,bObj) {
            var a = aObj.crisis_level
            var b = bObj.crisis_level
            var aCount = 0
            var bCount = 0
            var labelNumber = {
                A: 30,
                B: 20,
                C: 10
            }
            var levelNumber = {
                '+': 5,
                '-': -5
            }
            for (let l in labelNumber){
                if(a.indexOf(l)>=0) aCount+=labelNumber[l]
                if(b.indexOf(l)>=0) bCount+=labelNumber[l]
            }
            for (let l in levelNumber){
                if(a.indexOf(l)>=0) aCount+=levelNumber[l]
                if(b.indexOf(l)>=0) bCount+=levelNumber[l]
            }
            return aCount-bCount
        }
    }
}
</script>
